.CustomizeView-container {
    background-color: white;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: auto;
}

.CustomizeView-container h2 {
    text-align: center;
    padding: 0 20px 20px 20px;
    font-weight: 500;
    word-wrap: break-word;
}

.CustomizeView-switch {
    margin-left: auto;
}

.CustomizeView-form {
    display: flex;
    flex-direction: column;
    margin: 0 20px 40px 20px;
    flex: 1;
}

@media screen and (max-width: 960px) {
    .CustomizeView-container {
        height: 100%;
        margin-top: 0px;
    }
}